import React from 'react'
import _ from 'underscore'

import Stars from './Stars'
import Button from './Button'
import Answer from './Answer'
import Numbers from './Numbers'
import DoneFrame from './DoneFrame'

var possibleCombinationSum = function(arr, n) {
  if (arr.indexOf(n) >= 0) { return true; }
  if (arr[0] > n) { return false; }
  if (arr[arr.length - 1] > n) {
    arr.pop();
    return possibleCombinationSum(arr, n);
  }
  var listSize = arr.length, combinationsCount = (1 << listSize)
  for (var i = 1; i < combinationsCount ; i++ ) {
    var combinationSum = 0;
    for (var j=0 ; j < listSize ; j++) {
      if (i & (1 << j)) { combinationSum += arr[j]; }
    }
    if (n === combinationSum) { return true; }
  }
  return false;
};

export default class Game extends React.Component{
static randomNumber = () => 1 + Math.floor(Math.random() * 9)

  state = {
    selectedNumbers: [],
    numberOfStars: Game.randomNumber(),
    usedNumbers: [],
    answerIsCorrect: null,
    redraws: 5,
    doneStatus: null
  }

  selectNumber = (clickedNumber) => {
    if (this.state.selectedNumbers.indexOf(clickedNumber) >= 0 || this.state.usedNumbers.indexOf(clickedNumber) >= 0 ) {
      return;
    }

    this.setState({
      selectedNumbers: this.state.selectedNumbers.concat(clickedNumber),
      answerIsCorrect: null
    })
  }

  unselectNumber = (clickedNumber) => {

    this.setState({
      selectedNumbers: this.state.selectedNumbers.filter(number => number !== clickedNumber),
      answerIsCorrect: null
    })
  }

  checkAnswer = () => {
    this.setState({answerIsCorrect: this.state.numberOfStars === this.state.selectedNumbers.reduce((acc, n) => acc + n, 0)})

  }

  acceptAnswer = () => {
    this.setState(prevState => ({
      usedNumbers: prevState.usedNumbers.concat(this.state.selectedNumbers),
      selectedNumbers: [],
      answerIsCorrect: null,
      numberOfStars: Game.randomNumber()
    }), this.updateDoneStatus)
  }

  redraw = () => {
    if (this.state.redraws === 0) {
      return
    }

    this.setState(prevState => ({
      numberOfStars: Game.randomNumber(),
      answerIsCorrect: null,
      selectedNumbers: [],
      redraws: prevState.redraws - 1
    }), this.updateDoneStatus)

  }

  possibleSolutions = ({numberOfStars, usedNumbers}) => {
    const possibleNumbers = _.range(1, 10).filter(number => {
      usedNumbers.indexOf(number) === -1
    })
    return possibleCombinationSum(possibleNumbers, numberOfStars)
  }


  updateDoneStatus = () => {
    this.setState(prevState => {
      if (prevState.usedNumbers.length === 9) {
        return {doneStatus: 'Done. Nice!!'}
      }
      if (prevState.redraws === 0 && !this.possibleSolutions(prevState)) {
        return {doneStatus: 'Game Over!'}
      }
    })
  }

  render(){
    const {selectedNumbers, numberOfStars, answerIsCorrect, usedNumbers, redraws, doneStatus} = this.state

    return(
      <div className='container'>
        <h3>Play Nine</h3>
        <hr />
        <div className='row'>
          <Stars numberOfStars={numberOfStars}/>
          <Button
            redraws={redraws}
            selectedNumbers={selectedNumbers}
            checkAnswer={this.checkAnswer}
            acceptAnswer={this.acceptAnswer}
            answerIsCorrect={answerIsCorrect}
            redraw={this.redraw} />
          <Answer
            selectedNumbers={selectedNumbers}
            unselectNumber={this.unselectNumber} />
        </div>
        <br />
        {doneStatus ?
          <DoneFrame doneStatus={doneStatus}/> :
          <Numbers
            selectedNumbers={selectedNumbers}
            usedNumbers={usedNumbers}
            selectNumber={this.selectNumber}/>
        }
      </div>
    )
  }
}



