import React from 'react'

export default function Answer(props){
  return (
    <div className='col-5'>
      {props.selectedNumbers.map((number, index) =>
        <span
          key={index}
          onClick={() => props.unselectNumber(number)}>
          {number}
        </span>)}
    </div>
  )
}



